import { AxiosObservable } from "axios-observable"
import { injectable} from "inversify"
import "reflect-metadata"
import StringUtil from "../../utils/strings"
import Restful from "../Restful"
import IAccountService from "./IAccountService"

@injectable()
export default class AccountService implements IAccountService {
  signIn(email: string, password: string) {
    const url = "http://localhost:8081/metax/v1/auth/signin"
    return Restful.post(url, {
      requestId: StringUtil.generateRandom(20),
      email: email,
      password: password,
    })
  }

  signUp(email: string, password: string) {
    console.log("app port: ",process.env.APP_PORT)
    const url ="http://localhost:8081/metax/v1/auth/signup"
    return Restful.post(url, {
      requestId: StringUtil.generateRandom(20),
      email: email,
      password: password,
    }) 
  }

  changePassword(oldPass: string, newpass: string, renewPass: string): AxiosObservable<any> {
    const url ="http://localhost:8082/metax/v1/user-profile/password/change"
    return Restful.patch(url, {
      requestId: StringUtil.generateRandom(20),
      oldpass: oldPass,
      newpass: newpass,
      renewpass: renewPass
    }) 
  }

  activeAccount(userCode: string, activeCode: string): AxiosObservable<any> {
    const url =`http://localhost:8082/metax/v1/user-profile/active`
    return Restful.patch(url, {
      requestId: StringUtil.generateRandom(20),
      userCode: userCode,
      activeCode: activeCode,
    }) 
  }
  statistic(init: boolean): AxiosObservable<any> {
    return Restful.get(`http://localhost:8082/metax/v1/user-profile/statistic?init=${init}`)
  }
}
  
